import {bindable} from 'aurelia-framework';
import {LogManager} from 'aurelia-framework';
let log = LogManager.getLogger('dialog-info');

export class DialogInfoCustomElement {
  @bindable okHide;
  @bindable cancelHide;
  @bindable okClick;
  @bindable cancelClick;

  constructor() {
    this.title = '';
    this.messages = [];
    log.info('DialogInfoCustomElement');
  }

  openDialog(title, messages) {
    this.title = title;
    if (messages instanceof Array) {
      this.messages = messages;
    } else {
      this.messages = [messages];
    }
    this.mdlinfo.open();
  }

  onOK() {
    if (this.okClick) {
      this.okClick();
    }
  }

  onCancel() {
    if (this.cancelClick) {
      this.cancelClick();
    }
  }
}
