import {LogManager} from 'aurelia-framework';

let log = LogManager.getLogger('web-api');

export class WebAPI {
  isRequesting = false;

  resCheck(res) {
    if (!res.ok) {
      throw new Error(res.statusText);
    } else {
      return res.json();
    }
  }

  jsonCheck(json) {
    if (json.error) {
      throw new Error('[' + json.error.source.name + ']' + json.error.message);
    } else {
      return json;
    }
  }

  errorHandler(error) {
    log.info(error);
    throw new Error(error.message);
  }

  getContactList(page, lineCount, orderClass, orderItem, contactName, activeFlg) {
    this.isRequesting = true;
    let strUrl = 'https://izufr01.azurewebsites.net/api/SA21/01/';
    let skipRows = (page - 1) * lineCount;
    strUrl += skipRows + '/' + lineCount + (orderClass || orderItem || contactName || activeFlg ? '?' : '');
    if (orderClass) {
      strUrl += 'orderClass=' + orderClass + (orderItem ? '&' : '');
    }
    if (orderItem) {
      strUrl += 'orderItem=' + orderItem + (contactName ? '&' : '');
    }
    if (contactName !== null) {
      strUrl += 'contactName=' + contactName + (activeFlg ? '&' : '');
    }
    if (activeFlg) {
      strUrl += 'activeFlg=1';
    }
    return fetch(strUrl)
      .then(this.resCheck)
      .then(this.jsonCheck)
      .catch(this.errorHandler);
  }

  getContact(contactId) {
    this.isRequesting = true;
    let strUrl = 'https://izufr01.azurewebsites.net/api/SA21/01/';
    strUrl += contactId;
    return fetch(strUrl)
      .then(this.resCheck)
      .then(this.jsonCheck)
      .catch(this.errorHandler);
  }

  addContact(postData) {
    this.isRequesting = true;
    let strUrl = 'https://izufr01.azurewebsites.net/api/SA21/01/';
    return fetch(strUrl, {
      method: 'POST',
      headers: {'Content-Type': 'application/json'},
      body: JSON.stringify(postData)
    }).then(this.resCheck)
      .then(this.jsonCheck)
      .catch(this.errorHandler);
  }

  updateContact(postData) {
    this.isRequesting = true;
    let strUrl = 'https://izufr01.azurewebsites.net/api/SA21/01/';
    return fetch(strUrl, {
      method: 'PUT',
      headers: {'Content-Type': 'application/json'},
      body: JSON.stringify(postData)
    })
    .then(this.resCheck)
    .then(this.jsonCheck)
    .catch(this.errorHandler);
  }

  delContact(postData) {
    this.isRequesting = true;
    let strUrl = 'https://izufr01.azurewebsites.net/api/SA21/01/';
    return fetch(strUrl, {
      method: 'DELETE',
      headers: {'Content-Type': 'application/json'},
      body: JSON.stringify(postData)
    })
      .then(this.resCheck)
      .then(this.jsonCheck)
      .catch(this.errorHandler);
  }
}
