import {TaskQueue, inject} from 'aurelia-framework';
import {bindable, bindingMode} from 'aurelia-framework';
import {LogManager} from 'aurelia-framework';

let log = LogManager.getLogger('page-panel');

@inject(TaskQueue)
export class PagePanelCustomElement {
  @bindable({ defaultBindingMode: bindingMode.twoWay }) activePage;
  @bindable({ defaultBindingMode: bindingMode.twoWay }) maxPage;
  @bindable changePage;

  constructor(taskQueue) {
    this.taskQueue = taskQueue;
    this.pageSelector = false;

    this.subPaging(this.activePage, this.maxPage);
    log.info('PagePanelCustomElement');
  }

  subPaging(activePage, maxPage) {
    let pagingSize = 10;
    let startP = Math.floor(activePage / pagingSize) - (activePage % pagingSize === 0 ? 1 : 0);
    let endp = Math.floor(maxPage / pagingSize) - (maxPage % pagingSize === 0 ? 1 : 0);
    let is = startP < 0 ? 0 : startP * pagingSize;
    let ie = is + pagingSize;

    this.pages = [];
    if (startP > 0) {
      this.pages.push({disp: '<', page: is - 1 < 0 ? 0 : is - 1, act: (x, y) => {this.subPaging(x, y);}});
    }
    for (let i = is; i < ie; i++) {
      if (i < maxPage) {
        this.pages.push({disp: i + 1, page: i + 1, act: (x, y) => {this.directPageChange(x, y);}});
      }
    }
    if (startP < endp) {
      this.pages.push({disp: '>', page: ie + 1, act: (x, y) => {this.subPaging(x, y);}});
    }
  }

  directPageChange(x, y) {
    if (this.activePage !== x) {
      this.activePage = x;
      this.taskQueue.queueMicroTask(this.changePage);
      this.pageSelector = false;
    }
  }

  onFirstPage() {
    if (this.activePage > 1) {
      this.activePage = 1;
      this.taskQueue.queueMicroTask(this.changePage);
    }
    this.pageSelector = false;
  }

  onPrevPage() {
    if (this.activePage > 1) {
      this.activePage = this.activePage - 1;
      this.taskQueue.queueMicroTask(this.changePage);
    }
    this.pageSelector = false;
  }

  onNextPage() {
    if (this.activePage < this.maxPage) {
      this.activePage = this.activePage + 1;
      this.taskQueue.queueMicroTask(this.changePage);
    }
    this.pageSelector = false;
  }

  onLastPage() {
    if (this.activePage < this.maxPage) {
      this.activePage = this.maxPage;
      this.taskQueue.queueMicroTask(this.changePage);
    }
    this.pageSelector = false;
  }

  onPageSelect() {
    this.subPaging(this.activePage, this.maxPage);
    this.pageSelector = !this.pageSelector;
  }
}
