export class App {
  configureRouter(config, router) {
    config.title = 'Contacts';
    config.map([
      {route: '', moduleId: 'contact-list', name: 'contactList', title: 'List'},
      {route: 'contactNew/', moduleId: 'contact-new', name: 'contactNew'},
      {route: 'contactEdit/:id/', moduleId: 'contact-edit', name: 'contactEdit'}
    ]);
    this.router = router;
  }
}
