import {WebAPI} from './web-api';
import {TaskQueue, inject} from 'aurelia-framework';
import {LogManager} from 'aurelia-framework';
import moment from 'moment';
import {SesStorage} from './sesStorage';
import {Router} from 'aurelia-router';

let log = LogManager.getLogger('contact-list');

@inject(WebAPI, SesStorage, Router, TaskQueue)
export class ContactList {
  constructor(api, storage, router, taskQueue) {
    this.api = api;
    this.storage = storage;
    this.router = router;
    this.taskQueue = taskQueue;

    this.contacts = [];
    this.activePage = 1;
    this.maxPage = 1;
    this.lineCount = 10;

    this.selectedIdx = -1;
    this.dispLoading = false;

    this.orderClass = null;
    this.orderItem = null;
    this.displaySortingMark = false;

    this.dispSearchPanel = false;
    this.srchContactName = null;
    this.srchActiveFlg = false;
    this.condContactName = null;
    this.condActiveFlg = false;

    this.restoreOptions();
  }

  showList() {
    this.dispLoading = true;
    this.api.getContactList(
      this.activePage,
      this.lineCount,
      this.orderClass,
      this.orderItem,
      this.condContactName,
      this.condActiveFlg
    ).then(contacts => {
      this.contacts = contacts.map((e) => {
        e.selected = false;
        e.displayUpdated = moment(e.MCNTCT_UpdateDatetime).format('YYYY/MM/DD HH:mm:ss');
        return e;
      });
      if (contacts.length === 0) {
        // データが１件もない→何もしない
        log.info(`no data! ${this.activePage} ${this.maxPage}`);
      } else {
        // ページ情報再設定
        let allLineCount = contacts[0].LineCount;
        this.maxPage = Math.floor(allLineCount / this.lineCount);
        if (allLineCount % this.lineCount !== 0) {
          this.maxPage += 1;
        }

        // ソート順復元
        if (this.displaySortingMark) {
          let te = $("i[name='" + this.orderItem + "']");
          if (te) {
            if (this.orderClass % 2 === 0) {
              te.text('arrow_upward');
            } else {
              te.text('arrow_downward');
            }
          }
          this.displaySortingMark = false;
        }

        // 行選択状態復元
        if (this.selectedIdx >= 0 && this.selectedIdx < this.contacts.length) {
          this.contacts[this.selectedIdx].selected = true;
        } else {
          this.selectedIdx = -1;
        }
      }

      this.dispLoading = false;
    }).catch(error => {
      log.info(error);
      this.dispLoading = false;
      this.dlgerr.openDialog('連絡先一覧取得エラー', error.message);
    });
  }

  created() {
    this.showList();
  }

  onClickRow(idx) {
    if (this.selectedIdx === idx) {
      this.contacts[this.selectedIdx].selected = false;
      this.selectedIdx = -1;
    } else {
      if (this.selectedIdx >= 0) {
        this.contacts[this.selectedIdx].selected = false;
      }
      this.contacts[idx].selected = true;
      this.selectedIdx = idx;
    }
  }

  saveOptions() {
    let options = {};
    let optionsJson = '';

    options = {
      activePage: this.activePage,
      maxPage: this.maxPage,
      selectedIdx: this.selectedIdx,
      orderClass: this.orderClass,
      orderItem: this.orderItem,
      condContactName: this.condContactName,
      condActiveFlg: this.condActiveFlg
    };
    optionsJson = JSON.stringify(options);
    this.storage.set('options', optionsJson);
  }

  restoreOptions() {
    let optionsJson = null;
    let options = null;

    optionsJson = this.storage.get('options');
    if (optionsJson) {
      options = JSON.parse(optionsJson);
      this.activePage = options.activePage;
      this.maxPage = options.maxPage;
      this.selectedIdx = options.selectedIdx;
      this.orderClass = options.orderClass;
      this.orderItem = options.orderItem;
      this.displaySortingMark = this.orderClass ? true : false;
      this.condContactName = options.condContactName;
      this.condActiveFlg = options.condActiveFlg;
    }
  }

  onAdd() {
    this.saveOptions();
    this.router.navigate('/contactNew/');
  }

  onEdit() {
    if (this.selectedIdx >= 0) {
      this.saveOptions();
      this.router.navigate('/contactEdit/' + this.contacts[this.selectedIdx].MCNTCT_ContactCD);
    }
  }

  openDelModal() {
    $('.fixed-action-btn').closeFAB();
    this.dlginf.openDialog(
      '削除確認',
      [
        '選択した以下の連絡先を削除しますか？',
        this.contacts[this.selectedIdx].MCNTCT_ContactCD + '：' + this.contacts[this.selectedIdx].MCNTCT_ContactName
      ]
    );
  }

  onDelAgree() {
    this.dispLoading = true;
    let body = [{
      'contactCD': this.contacts[this.selectedIdx].MCNTCT_ContactCD
    }];
    this.api.delContact(body).then(contacts => {
      this.contacts[this.selectedIdx].selected = false;
      this.selectedIdx = -1;
      this.dispLoading = false;
      this.showList();
    }).catch(error => {
      log.info(error);
      this.dispLoading = false;
      this.dlgerr.openDialog('連絡先削除エラー', error.message);
    });
  }

  sortClick(e) {
    let targ = $(e.target);
    let elem = null;
    if (targ && targ.prop('tagName').toUpperCase() === 'I') {
      elem = targ;
    } else if (targ.find('i') && targ.find('i').prop('tagName').toUpperCase() === 'I') {
      elem = targ.find('i');
    } else {
      return;
    }

    if (this.orderItem === null) {
      elem.text('arrow_downward');
      this.orderItem = elem.attr('name');
      this.orderClass = 1;
    } else if (this.orderItem === elem.attr('name')) {
      if (elem.text() === 'arrow_downward') {
        elem.text('arrow_upward');
        this.orderClass = 2;
      } else {
        elem.text('arrow_downward');
        this.orderClass = 1;
      }
    } else {
      let te = $("i[name='" + this.orderItem + "']");
      if (te) {
        te.text('sort');
      }
      elem.text('arrow_downward');
      this.orderItem = elem.attr('name');
      this.orderClass = 1;
    }

    if (this.orderItem === 'MCNTCT_ContactName' || this.orderItem === 'MCNTCT_UpdateEmployeeCD') {
      this.orderClass += 2;
    }
    this.showList();
  }

  openSearchPanel() {
    if (this.dispSearchPanel) {
      this.dispSearchPanel = false;
    } else {
      this.srchContactName = this.condContactName;
      this.srchActiveFlg = this.condActiveFlg;
      this.dispSearchPanel = true;
    }
  }

  changeSearchCondition() {
    let strContactName = this.srchContactName ? this.srchContactName : '';
    if (strContactName.trim().length === 0) {
      this.condContactName = null;
    } else {
      this.condContactName = strContactName.trim();
    }
    this.condActiveFlg = this.srchActiveFlg;
    this.dispSearchPanel = false;
    this.showList();
  }

  changePageByPagingPanel() {
    if (this.selectedIdx >= 0 && this.contacts.length > 0) {
      this.contacts[this.selectedIdx].selected = false;
    }
    this.selectedIdx = -1;
    this.showList();
  }
}
